import { ViewWrapper } from '../components/molecules/ViewWrapper/ViewWrapper';
import { StyledLink } from '../components/organisms/Navigation/Navigation.styles';
import { Title } from '../components/atoms/Title/Title';
import { Text } from '../components/atoms/Text/Text';
import UsersList from '../components/organisms/UsersList/UsersList';

const UserChoice = () => {
  return (
    <>
      <ViewWrapper>
        <Title>Lista uytkownikw</Title>
        <Text>Wybierz uytkownika z listy, lub dodaj nowego.</Text>
        <UsersList />
        <StyledLink to="/dodaj-uzytkownika">Dodaj uytkownika</StyledLink>
      </ViewWrapper>
      <ViewWrapper>
        <Text>
        Monitoruj stan swojego ciaa oraz dowiedz si wicej na jego temat. Zdobd dane przy uyciu elektronicznej
          wagi z analiz skadu ciaa. Co tydzie dodawaj i interpretuj swoje postpy. yczymy powodzenia.
        </Text>
      </ViewWrapper>
    </>
  );
};

export default UserChoice;
