import { ViewWrapper } from '../components/molecules/ViewWrapper/ViewWrapper';
import { Title } from '../components/atoms/Title/Title';
import { Text } from '../components/atoms/Text/Text';

const Advices = () => {
  return (
    <>
      <ViewWrapper>
        <Title>Jak zdrowo schudn? Poznaj zasady zdrowego odchudzania.</Title>
        <Text>
          Jedynym sposobem na zdrowe odchudzanie jest ograniczenie jedzenia, zmiana diety i zwikszenie aktywnoci fizycznej.
          Nie zastpi tego adne tabletki, herbatki czy suplementy diety. Mog wspomc metabolizm lub dostarczy brakujcych
          witamin, ale same z siebie nie spowoduj zrzucenia wagi. Zdrowe odchudzanie wymaga sumiennoci i cierpliwoci.
          Nie istnieje co takiego jak dieta cud i magiczne produkty oraz potrawy dziki ktrym zrzucimy zbdne kilogramy.

        </Text>
      </ViewWrapper>

      <ViewWrapper>
        <Title>Regularno</Title>
        <Text>
          Lepiej dla naszego organizmu, jeeli dostarczamy mu posiki regularnie w mniejszych ilociach,
          wtedy atwiej spala kalorie i nie magazynuje zapasw. Wane jest jedzenie niadania daje nam
          to wicej energii i zwiksza koncentracj, pobudza take organizm do dziaania. Trzeba stara
          si je o staych porach, eby organizm si do tego przyzwyczai, a zniknie ochota na podjadanie.
          Zaspokojenie godu nastpuje dopiero po 20 minutach od rozpoczcia posiku, dlatego starajmy si
          nie spieszy si z jedzeniem, poniewa moemy dostarczy, wicej ni potrzebujemy. Najpniej 2-3
          godziny przed snem powinnimy z konsumowa ostatni posiek, aby nasz organizm nie pracowa podczas
          snu, poniewa nie funkcjonuje wtedy w prawidowy sposb i le moe rozprowadzi elementy co moe
          skutkowa zwikszeniem si masy.
        </Text>
      </ViewWrapper>

      <ViewWrapper>
        <Title>Urozmaicona dieta</Title>
        <Text>
          Jedzenie tych samych rzeczy kadego dnia moe powodowa niedobory w naszym organizmie. Wane jest,
          aby dostarczy mu wanych skadnikw odywczych takich jak biaka, wglowodany, tuszcze, witaminy
          oraz skadniki mineralne.
          <br />
          <br />
          <strong>Biaka</strong>
          <br />
          <br />

          Ich penowartociowym rdem jest mleko i jego przetwory. Biae miso jak kurczak, indyk oraz ryby.
          Spoycie biaka nie powinno si ogranicza na produkty pochodzenia zwierzcego. Rwnie dobrym jego
          rdem s warzywa strczkowe, pistacje, migday, orzechy nerkowca, woskie i laskowe. Biako powinno
          znajdowa si w kadym posiku, poniewa jest gwnym budulcem naszych tkanek i pozwala na dusze
          zaspokojenie godu.

          <br />
          <br />
          <strong> Wglowodany</strong>
          <br />
          <br />

          Najlepszym rdem s tzw. wglowodany zoone dlatego, e wchaniaj si wolniej i stopniowo uwalniaj
          energi. Nale one do grupy produktw z penego przemiau m. in. pieczywo penoziarniste, brzowy ry,
          patki owsiane i grube kaszy. Nie mona zapomina, e wglowodany to rwnie warzywa, pobudzaj one
          przemian energii i dostarczaj witaminy z grupy B. Unika trzeba wglowodanw prostych takich jak np.
          biae pieczywo, ciastka, demy. Zapewniaj one znacznie mniej energii za to wicej kalorii.

          <br />
          <br />
          <strong>Tuszcze</strong>
          <br />
          <br />

          Powinno si wybiera tuszcze rolinne takie jak olej rzepakowy, oliwa czy pestki sonecznika, s one
          mniej cikostrawne i nie podwyszaj tak cholesterolu jak zwierzce. To nie znaczy, eby musimy
          cakowicie wyczy tuszcze zwierzce z naszej diety. Moemy je podmieni np. wieprzowin - drobiem
          lub rybami, mietan - jogurtem lub maso - margaryn wysokogatunkow. Dla przykadu 100 gram misa
          drobiowego zawiera dwa razy wicej biaka, posiada dwa razy mniej kalorii i tyle samo mniej tuszczu
          ni taki sam 100 gramowy kawaek boczku. Wane jest te to w jaki sposb przygotowujemy dania, jeeli
          chcemy zmniejszy zawarto tuszczu w daniach powinnimy zrezygnowa ze smaenia na rzecz pieczenia,
          duszenia, gotowania lub grillowania.

        </Text>
      </ViewWrapper>

      <ViewWrapper>
        <Title>Bonnik</Title>
        <Text>
          Jest bardzo wanym elementem w posiku, poniewa wchania wod i pcznieje w odku dajc uczucie sytoci.
          Poprawia prac jelit i oczyszcza organizm z toksyn. Najwicej bonnika zawieraj produkty penoziarniste
          oraz warzywa i owoce. Trzeba jednak uwaa, poniewa niektre zawieraj rwnie duo cukru, a zatem i kalorii
          jak banany czy winogrona. Bez obawy moemy natomiast je jabka, maliny, porzeczki, truskawki.
        </Text>
        <Title>Nawodnienie</Title>
        <Text>
          Okoo 70% organizmu ludzkiego skada si z wody, dlatego bardzo wane jest zadbanie o jej odpowiednie
          dostarczenie. Zakada si, e powinno si wypija codziennie 2-4% masy ciaa. Woda jest gwnym skadnikiem
          budulcowym, reguluje temperatur ciaa, nawila skr i usuwa toksyny. Woda nie posiada kalorii dlatego jest
          lepszym rdem nawodnienia ni napoje sodzce czy alkohol, te za to substancje s pustymi kaloriami, poniewa
          nie dostarczaj odpowiednich zapasw energii.
        </Text>
        <Title>Przemiana materii</Title>
        <Text>
          Moemy w atwy i bezpieczny sposb podkrci nasz przemian materii podczas diety. Ostre potrawy dziki
          dodawanym do nich przyprawom takim jak pieprz, chilli, imbir itp stymuluj wytwarzanie ciepa w organizmie,
          co przekada si na wikszy wysiek dla organizmu i spalanie kalorii. Niektre z nich maj dodatkowe waciwoci
          np. zawarty w imbirze gingerol przyspiesza spalanie tuszczu.
        </Text>
      </ViewWrapper>

      <ViewWrapper>
        <Title>Sport</Title>
        <Text>
          Same uprawianie sportu niekoniecznie spowoduje schudnicie, jeeli nadal bdziemy jedli za du ilo kalorii
          ni mamy zapotrzebowania. W przypadku prby utrzymania deficytu kalorycznego jest to bardzo dobre poczenie,
          poniewa nie tylko dostarczymy mniej kalorii co spowoduje chudnicie, ale stracimy ich jeszcze wicej dziki
          uprawianiu sportu. Wane jest, aby trening trwa przynajmniej 45 minut, poniewa dopiero po 30 minutach zaczynamy
          spala tkank tuszczow. Najlepsze bd wiczenia aerobowe, czyli bieganie, jazda na rowerze, pywanie.
          wiczenia zmniejszaj poziom hormonw sprzyjajcych tyciu jak np. kortyzol, dziki wydzielaniu si endorfin.
        </Text>
      </ViewWrapper>
    </>
  );
};

export default Advices;
