import { useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { Title } from '../components/atoms/Title/Title';
import FormCpm from '../components/organisms/Form/FormCpm';
import { ViewWrapper } from '../components/molecules/ViewWrapper/ViewWrapper';
import { Text } from '../components/atoms/Text/Text';
import { UsersContext } from '../providers/UsersProvider';

const Cpm = () => {
  const { currentUser } = useContext(UsersContext);
  const navigate = useNavigate();

  const redirectToUsersList = () => {
    const path = '/';
    navigate(path);
  };

  useEffect(() => {
    if (!currentUser.hasOwnProperty('id')) redirectToUsersList();
  }, []);

  return (
    <>
      <ViewWrapper>
        <Title>Oblicz cakowit przemian materii</Title>
        <FormCpm />
      </ViewWrapper>
      <ViewWrapper>
        <Text>
          <strong>Cakowita przemiana materii (CPM)</strong> jest to czna suma dobowych wydatkw energetycznych
          powizanych z podstawow przemian materii (PPM) i wspczynnikiem aktywnoci fizycznej (PAL).
        </Text>
        <Text>
          <strong>PPM</strong> jest to najniszy poziom przemiany materii, ktry wskazuje na to ile warunkowo
          potrzebujemy dostarczy energii organizmowi, aby zaspokoi podstawowe funkcje yciowe w optymalnych warunkach.
        </Text>
        <Text>
          <strong>PAL</strong> jest to wspczynnik, ktry okrela stopie codziennej aktywnoci fizycznej. Pozwoli on
          na oszacowanie cakowitego wydatku energetycznego.
        </Text>
        <Text>Cakowit przemian materii moemy obliczy ze wzoru:</Text>
        <Text>
          <strong>CPM = PPM  PAL</strong>
        </Text>
        <Text>
          Zalenie od otrzymanego wyniku w przypadku w ktrym chcemy schudn. Dzienna warto kaloryczna diety powinna
          by nisza ni otrzymana warto. Jest to tak zwany ujemny bilans energetyczny. W przypadku utrzymania przez
          du- szy czas owego bilansu nasza waga bdzie si zmniejsza, ale musimy zadba, aby spoywane posiki byy
          bogate w wartoci potrzebne organizmowi.
        </Text>
      </ViewWrapper>
    </>
  );
};

export default Cpm;
